


























import Vue from "vue";
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
import Reveal from "../components/Reveal.vue";
import api from "../api/index";
import IResp, { IArticle } from "../types/resp";

interface Idata {
  currPage: number;
  count: number;
  dir: IArticle[] | null;
}

export default Vue.extend({
  name: "Home",
  data(): Idata {
    return {
      currPage: 0,
      count: 0,
      dir: null
    };
  },
  created() {
    this.getData();
  },
  computed: {
    loading() {
      if (!this.dir) return true;
      else return false;
    }
  },
  methods: {
    async getData() {
      this.dir = null
      this.count = await this.getCount();
      this.dir = await this.getDir(this.currPage);
    },
    getCount() {
      return api.count().then(res => res.count);
    },
    getDir(page: number) {
      return api.getTable(page).then(res => res.dir);
    },
    currentChange(val: number) {
      this.currPage = val - 1;
      this.getData();
    },
    sizeChange(val: number) {},

    tranTime(str: string) {
      return new Date(str).toLocaleDateString().replace(/\//g, "-");
    }
  },
  components: {
    Header,
    Footer,
    Reveal
  }
});
