









































import Vue from "vue";
import api from "../api/index";
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
import IResp from "../types/resp";
import util from "../util";

export default Vue.extend({
  data() {
    return {
      userName: "",
      password: "",
      cookie: ""
    };
  },
  methods: {
    async login() {
      const isOk = await this.fetchLogin();
      if (isOk) this.handleCookie();
      else alert("登录失败");
    },
    async fetchLogin(): Promise<boolean> {
      const res: IResp = await api.login(this.user());
      if (res.code != "success" || res.token == null) return false;
      this.cookie = res.token;
      return true;
    },
    user(): { userName: string; password: string } {
      return { userName: this.userName, password: this.password };
    },
    handleCookie() {
      if (util.getCookie() != this.cookie) util.setCookie(this.cookie);
      this.gotoAdmin();
    },
    gotoAdmin() {
      this.$router.replace({ name: 'Admin'})
    }
  },
  components: {
    Header,
    Footer
  }
});
